import helper from '../../common/helper.js';
export default {
	data() {
		return {
			userfoArray: '',
		};
	},
	onLoad() {
		 this.userinfo()
	},
	onShow() {
		// this.userinfo()
	},
	onPullDownRefresh: function() { //下拉刷新
		this.userinfo(true);
	},
	methods: {
		// 获取个人信息
		userinfo(isdown) {
			uni.showLoading({
				mask: true,
				title: '加载中'
			});
			var userId = helper.getstate().userid;
			uni.request({
				url: helper.websiteUrl + 'yiru/sdl/merchantcenter/getmerchantinfo',
				method: 'GET',
				data: helper.postdata({
					mid : helper.getstate().userid
				}),
				success: res => {
					if (res.data.code == 200) {
						this.userfoArray = res.data.data.baseinfo;
					}else{
					uni.showToast({
						icon: 'none',
						title: '网络异常,请稍后重试'
					});
					}
					uni.hideLoading();
				},
				fail: () => {
					uni.hideLoading();
					uni.showToast({
						icon: 'none',
						title: '网络异常,请稍后重试'
					});
				},
				complete: () => {
					uni.hideLoading();
					if (isdown) {
						uni.stopPullDownRefresh(); //刷新停止
					}
				}
			});
		},
		opendata() { // 打开个人资料
			uni.navigateTo({
				url: 'MerInfoData',
			});
		},
		openaccount() { //打开我的账户
			uni.navigateTo({
				url: 'MerPayRank',
			});
		},
		openmessages() { //打开系统消息
			uni.navigateTo({
				url: '../my/messages',
			});
		},
		openhelp() { //打开帮助中心
			uni.navigateTo({
				url: '../my/help',
			});
		},
		openzhanghu() { //打开账户安全
			uni.navigateTo({
				url: 'MerSafety',
			});
		},
		logout() {
			uni.showModal({
				title: '提示',
				content: '是否确认退出登录！',
				success: function(res) {
					if (res.confirm) {
						helper.logout_Mer();
						uni.showToast({
							icon: 'none',
							title: '您已退出登录'
						})
					}
				}
			});
		},
		// 打开商家任务
		opentask() {
			uni.redirectTo({
				url: 'MerTask'
			});
		},
		// 打开首页
		openindex() {
			uni.redirectTo({
				url: 'MerIndex'
			});
		}

	}
}
